import React from "react"
import PropTypes from "prop-types"
import "./layout.css"
import { createGlobalStyle } from "styled-components"
import NavBar from "../components/NavBar"
import { graphql, useStaticQuery } from "gatsby"
import { Container } from "../components/PostLayout"

const colorPalette = {
  lightShades: "#ffffff",
  lightAccent: "#b7b7f6",
  mainBrand: "#acdbdf",
  darkAccent: "#414656",
  darkShades: "#010101",

  success: "#e2e3e1",
  warning: "#ff7f27",
  error: "#fc5959"
}

const baseTheme = {
  actions: {
    error: colorPalette.error,
    info: colorPalette.darkShades,
    primary: colorPalette.mainBrand,
    success: colorPalette.success,
    warning: colorPalette.warning
  },
  palette: {
    darkAccent: colorPalette.darkAccent,
    darkShades: colorPalette.darkShades,
    lightAccent: colorPalette.lightAccent,
    lightShades: colorPalette.lightShades,
    mainBrand: colorPalette.mainBrand
  }
}

const theme = {
  ...baseTheme,
  global: {
    bg: colorPalette.darkShades,
    color: colorPalette.lightShades,
    link: colorPalette.mainBrand,
    linkHover: colorPalette.lightAccent
  }
}

const GlobalStyle = createGlobalStyle`
  html {
    height: 100%;
    font-family: cursive;
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    overflow: auto;
  }
  body {
  background-color: #eae6d8;
    color: #263238;
    background-image: url(${p => p.bg});
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    transition: background 0.2s ease-out;
    background-size: 10%;
  }
`

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
{
  file(relativePath: {eq: "images/bgbrick.png"}) {
    childImageSharp {
      fluid(traceSVG: {threshold: 100}) {
        srcWebp
      }
    }
  }
}
`)
  return (
    <>
      <GlobalStyle bg={data.file.childImageSharp.fluid.srcWebp} theme={theme}/>
      <NavBar/>
      <Container>
        {children}
      </Container>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired
}

export default Layout
